                            <template v-if="item.id === 'Cash' && allBodyParts[index].bodyPartId !== 'Other'">
                              <template v-for="(item, i) in cashData[index]" :key="i">
                                <div class="row align-items-center mb-2">
                                  <div class="col-4">
                                    <div class="input-group">
                                      <select class="rounded-pill form-select text-main-dark2"
                                              :class="{ 'text-white' : item.disabled }"
                                              :disabled="item.disabled"
                                              v-model="item.priceType"
                                              @change="changePriceType(item, index)">
                                        <option v-for="item in initUpdateBasicDataAndFeeData.priceTypes"
                                                :value="item.id"
                                                :key="item.id">{{ item.name }}
                                        </option>
                                      </select>
                                    </div>
                                  </div>
                                  <div class="col-4" v-if="item.priceType !== 'Deposit'">
                                    <div class="d-flex align-items-center">
                                      <input type="number" class="form-control rounded-pill"
                                            v-model="item.price" @blur="cashInputBlur(item, index)"
                                            min="0"
                                            :disabled="item.disabled">
                                    </div>
                                  </div>
                                  <div class="col-4">
                                    <div class="input-group">
                                      <select class="rounded-pill form-select text-white"
                                              disabled
                                              v-model="item.received">
                                        <option :value="false">費用未收</option>
                                        <option :value="true">費用已收</option>
                                      </select>
                                    </div>
                                  </div>
                                </div>
                                <!-- 押金費用時，開啟 -->
                                <template v-if="item.priceType === 'Deposit'">
                                  <div class="row align-items-center mb-2" v-for="(depositDataItem, i) in item.depositData" :key="i">
                                    <div class="col-4">
                                      <div class="input-group">
                                        <select class="rounded-pill form-select text-main-dark2"
                                                v-model="depositDataItem.voucherType"
                                                :disabled="item.disabled"
                                                :class="{ 'text-white' : item.disabled }">
                                          <template v-for="voucherTypeItem in initUpdateBasicDataAndFeeData.voucherTypes"
                                                  :key="voucherTypeItem.id">
                                            <option :value="voucherTypeItem.id"
                                                    v-if="voucherTypesJudge(item.depositData, voucherTypeItem.id, depositDataItem.voucherType)">
                                                    {{ voucherTypeItem.name }}
                                            </option>
                                          </template>
                                        </select>
                                      </div>
                                    </div>
                                    <div class="col-4">
                                      <div class="d-flex align-items-center">
                                        <input type="number" class="form-control rounded-pill"
                                              @blur="cashInputBlur(item, index)"
                                              :disabled="item.disabled"
                                              :class="{ 'text-white' : item.disabled }"
                                              v-model="depositDataItem.amountOfMoney"
                                              min="0">
                                      </div>
                                    </div>
                                    
                                    <div class="col-2" v-if="!item.disabled">
                                      <a href="#" class="me-2"
                                        @click.prevent="deleteDepositInput(index, i)"
                                        v-if="item.depositData.length !== 1">
                                        <img src="@/assets/stylesheets/icon/--05.png" alt="" width="20" height="20">
                                      </a>
                                      <a href="#" @click.prevent="addDepositInput(index, item.depositData)"
                                        v-if="item.depositData.length === 1 || item.depositData.length === (i + 1) && initUpdateBasicDataAndFeeData.voucherTypes.length > item.depositData.length">
                                        <img src="@/assets/stylesheets/icon/+-04.png" alt="" width="20" height="20">
                                      </a>
                                    </div>
                                  </div>
                                </template>         
                              </template>
                            </template>

                            <!-- 現金支付 - 其他部位 -->
                            <template v-if="item.id === 'Cash' && allBodyParts[index].bodyPartId === 'Other'">
                              <div class="row align-items-center mb-2"
                                  v-for="(item, i) in cashData[index]"
                                  :key="i">
                                
                                <div class="col-4">
                                  <div class="input-group">
                                    <select class="rounded-pill form-select text-main-dark2"
                                            v-model="item.priceType">
                                      <option v-for="item in priceTypesForOtherBodyparts"
                                              :value="item.id"
                                              :key="item.id">{{ item.name }}
                                      </option>
                                    </select>
                                  </div>
                                </div>
                                <div class="col-4">
                                  <div class="d-flex align-items-center">
                                    <input type="number" class="form-control rounded-pill"
                                          v-model="item.price" @blur="cashInputBlur(item, index)"
                                          min="0">
                                  </div>
                                </div>
                                <div class="col-4">
                                  <div class="input-group">
                                    <select class="rounded-pill form-select text-white"
                                            disabled
                                            v-model="item.received">
                                      <option :value="false">費用未收</option>
                                      <option :value="true">費用已收</option>
                                    </select>
                                  </div>
                                </div>
                              </div>
                            </template>

                            <!-- 活動優惠券 -->
                            <div v-if="item.id === 'Activity'">
                                <div class="row align-items-center mb-2" v-for="(item, i) in activityData[index]" :key="i">
                                  <div class="col-6">
                                    <div class="input-group">
                                      <select class="rounded-pill form-select" v-model="item.prizeId" disabled>
                                        <option v-for="item in initUpdateBasicDataAndFeeData.prizeData"
                                                :value="item.prizeId"
                                                :key="item.prizeId">{{ item.activityName }}
                                        </option>
                                      </select>
                                    </div>
                                  </div>
                                  <div class="col-4">
                                    <div class="d-flex align-items-center">
                                      <input type="text" class="form-control rounded-pill" placeholder="卷號" disabled
                                            v-model="item.couponNumber">
                                    </div>
                                  </div>

                                </div>
                              </div>

                          </div>
                        </div>
                      </div>
                    </div>
                  </template>
                </div>

              </div>

            </div>
          </div>

          <!-- modal footer -->
          <div class="row gx-0">
            <div class="col-2 bg-gray1 py-6 px-10" style="border-bottom-left-radius: 20px;"></div>
            <div class="col-10 py-4 ps-10 pe-5 border-2 border-top border-gray1">
              <!-- 未登錄 -->
              <div class="d-flex align-items-center">

                <label class="me-12" for="">總金額</label>
                <input type="text"
                      disabled
                      class="form-control w-auto d-inline-block rounded-pill me-auto" placeholder="0"
                      v-model="total">

                <div class="d-inline-block">
                  <button type="button" class="btn btn-alert-second rounded-pill" data-bs-dismiss="modal">關 閉</button>
                  <button type="button" class="btn btn-alert-main rounded-pill ms-2"
                          @click="executeUpdateMedicalRecord()">確 定</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import { onMounted, ref, inject, toRefs } from 'vue';
import { Modal } from 'bootstrap';
import { useRoute  } from 'vue-router';
import AlertMessage from '@/components/AlertMessage';

export default {
  name: 'medicalRecordIndex',
  components: {
    AlertMessage,
  },
  setup() {
    const alertMessage = inject('alertMessage');
    const textConversionTool = inject('textConversionTool');

    const fetchFunction = inject('fetchFunction');

    const {
      fetchApi,
      getTokenFromCookie
    } = fetchFunction;

    const { updateMessage } = alertMessage;

    const {
      deleteTTimeFormat,
      convertTimeFormatSlash,
      clinicPeriodToCh,
      convertTimeFormatDeleteSecond,
    } = textConversionTool;

    const Employee = inject('Employee');

    const {
      employeeState,
      getAllDoctorData,
      getBeauticians,
      getBeauticianAAByActiveStatus,
    } = Employee;

    const {
      beauticiansData,
      allDoctorData,
      beauticianAAData
      } = toRefs(employeeState);

    const courseType = inject('courseType');

    const {
      courseTypeState,
      getCourseTypeData,
    } = courseType;

    const {
      courseTypeData
    } = toRefs(courseTypeState);

    const MedicalRecord = inject('MedicalRecord');

    const {
      medicalRecordState,
      getMedicalRecordStampData,
    } = MedicalRecord;

    const {
      medicalRecordStampData
    } = toRefs(medicalRecordState);

    const customerNeedsModalRef = ref(null);
    let customerNeedsModal;

    const laserEnergyRef = ref(null);
    let laserEnergyRefModal;

    const anesthesiaRecordRef = ref(null);
    let anesthesiaRecordRefModal;

    const deleteDataRef = ref(null);
    let deleteDataRefModal;

    const freeBillRef = ref(null);
    let freeBillRefModal;

    const historicalMedicalRecordsRef = ref(null);
    let historicalMedicalRecordsRefModel;

    const contactRecordsModalRef = ref(null);
    let contactRecordsModalRefModel;

    const signatureFunctionModalRef = ref(null);
    let signatureFunctionModal;

    const cancelfreeBillRef = ref(null);
    let cancelfreeBillRefModal;

    const editMedicalRecordRef = ref(null);
    let editMedicalRecordRefModal;

    const route = useRoute();

    let params;

    onMounted(() => {
      customerNeedsModal = new Modal(customerNeedsModalRef.value);
      laserEnergyRefModal = new Modal(laserEnergyRef.value);
      anesthesiaRecordRefModal = new Modal(anesthesiaRecordRef.value);
      deleteDataRefModal = new Modal(deleteDataRef.value);
      freeBillRefModal = new Modal(freeBillRef.value);
      historicalMedicalRecordsRefModel = new Modal(historicalMedicalRecordsRef.value);
      contactRecordsModalRefModel = new Modal(contactRecordsModalRef.value);
      signatureFunctionModal = new Modal(signatureFunctionModalRef.value);
      cancelfreeBillRefModal = new Modal(cancelfreeBillRef.value);
      editMedicalRecordRefModal = new Modal(editMedicalRecordRef.value);

      // medicalRecordId.value = route.params.medicalRecordId;
    })

    const historyCourseWindow = ref(false);

    /** 開啟之前治療的方案 */
    const openHistoryCourseWindow = () => {
      if (historyCourseWindow.value) return null;
      historyCourseWindow.value = true;

      function close() {
        historyCourseWindow.value = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    const courseTypesList = ref([]);       // 右上病歷用的標籤
    const courseTypesIndex = ref(0);
    const medicalRecordTagsList = ref([]); // 病歷內的標籤
    // let medicalRecordId;                   // 被選擇的病歷
    const medicalRecordId = ref(null);     // 被選擇的病歷
    const medicalRecordIdIndex = ref(0);   // 被選擇的病歷 index
    let medicalRecordTagsAll = [];         // 全部的病歷標籤

    /** 取得編輯病歷用的標籤 */
    async function getUpdateMedicalRecordTags() {
      const url = `/api/MedicalRecord/GetUpdateMedicalRecordTags?customerId=${params.customerId}&appointmentDate=${params.date}`;
      
      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      const { courseTypes, medicalRecordTags } = response.data;
      courseTypesList.value = courseTypes;

      // 預設療程類型 index
      courseTypesIndex.value = 0;

      medicalRecordTagsAll = medicalRecordTags;

      medicalRecordTagsList.value = medicalRecordTagsAll.filter(item => item.courseType === courseTypesList.value[courseTypesIndex.value].id);

      // 預設病歷標籤 index
      medicalRecordIdIndex.value = 0;
      medicalRecordId.value = medicalRecordTagsList.value[medicalRecordIdIndex.value].medicalRecordId;
    }

    /** 選擇病歷內大標籤 - 療程類型 */
    function selectMedicalRecordCourseType(index) {
      courseTypesIndex.value = index;
      medicalRecordTagsList.value = medicalRecordTagsAll.filter(item => item.courseType === courseTypesList.value[courseTypesIndex.value].id);

      medicalRecordIdIndex.value = 0;
      medicalRecordId.value = medicalRecordTagsList.value[medicalRecordIdIndex.value].medicalRecordId;

      getInitUpdateMedicalRecordData();
    }
    
    /** 選擇病歷內小標籤 - 病歷標籤 */
    function selectMedicalRecordMedicalRecordTag(index) {
      medicalRecordIdIndex.value = index;
      medicalRecordId.value = medicalRecordTagsList.value[medicalRecordIdIndex.value].medicalRecordId;

      getInitUpdateMedicalRecordData();
    }

    const medicalRecordData = ref({});
    const medicineQuickSelections = ref([]);

    const medicineQuickSelectionDetailData = ref({});
    const medicineGroupClassificationData = ref({});
    const medicineOptionData = ref(null);

    const takeMedicineFrequencyData = ref([]);
    const currentData = ref({});

    const detailedAnswer = ref([]); // 醫師詳答
    const addMedicineData = ref([]); // 新增藥品資料

    /** 取得初始化編輯病歷用的資料 */
    async function getInitUpdateMedicalRecordData() {
      const url = `/api/MedicalRecord/GetInitUpdateMedicalRecordData?medicalRecordId=${medicalRecordId.value}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      console.log(response, 'test');

      dataCollation(response.data);
    }

    /** 取得所有服藥頻率 */
    async function takeMedicineFrequency() {
      const url = `/api/TakeMedicineFrequency/GetAll`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      takeMedicineFrequencyData.value = response.data;
    }

    const medicineTypeData = ref([]);

    /** 取得所有藥品類型 */
    async function medicineType() {
      const url = `/api/MedicineType/GetAll`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      medicineTypeData.value = response.data;
    }

    const prescribeMedicineRecordsToDeleteOredit = ref([]);

    const operator = ref(null);    // 醫師 id
    const customerId = ref(null);
    const arrivedTime = ref(null); // 到診日期

    /** 初始化編輯病歷整理 */
    async function dataCollation(response) {
      await getAnestheticUsageRecord();
      await getLaserRecordByMedicalReocrd();
      await getDoctorFreeRecordByMedicalRecord();
      await getSubcourseData();
      await getMedicalRecordStampData(medicalRecordId.value);
      
      // 左邊
      await getCustomerTraitAll();
      await getCustomerBasicData(params.customerId);
      await getCustomerDemandNamesById();

      console.log(response, '初始化編輯病歷整理');

      const { medicalRecord } = response;

      operator.value = medicalRecord.operator;
      customerId.value = medicalRecord.customerId;
      arrivedTime.value = deleteTTimeFormat(medicalRecord.arrivedTime).date;

      console.log(arrivedTime.value, '初始化編輯病歷整理');

      // 病歷是點滴，取得客戶最後使用點滴耗材使用的紀錄
      if (response.medicalRecord.courseType === 'IV') {
        await getLastIvConsumableRecord(params.customerId, response.medicalRecord.id);
      }

      // 醫師詳答
      let detailedAnswerItem = [];
      detailedAnswer.value = [];

      response.doctorDetailGroupOptionData.forEach(itemA => {
        itemA.doctorDetailGroupItems.forEach(itemB => {
          if (response.doctorDetailAnswerData.includes(itemB.id)) {
            if (itemA.groupName === '雷射能量') {
              detailedAnswerItem.push(itemB);
            } else {
              detailedAnswer.value.push(itemB);
            }
          }
        })

        if (itemA.groupName === '雷射能量') {
          detailedAnswer.value.push(detailedAnswerItem);
        }

        detailedAnswerItem = [];
      })

      console.log(detailedAnswer.value);

      // 新增藥品
      addMedicineData.value = [
        {
          medicineTypeData: null,
          medicineGroupId: null,
          medicineId: null,
          amount: 0,
          takeMedicineFrequencyId: null,
        }
      ];

      // 藥品選擇
      medicineQuickSelections.value = response.prescribeMedicineData.medicineQuickSelections;

      // 藥品選擇 - 上一次的選擇的資料，用於修改、刪除
      prescribeMedicineRecordsToDeleteOredit.value = response.prescribeMedicineRecords.map(item => {
        return {
          medicineTypeData: item.medicineType,
          medicineGroupId: item.medicineGroupId,
          medicineId: item.medicineId,
          amount: item.amount,
          takeMedicineFrequencyId: item.takeMedicineFrequencyId,
        }
      });

      // 新增藥品 - 下拉 (口服)
      medicineQuickSelectionDetailData.value = response.prescribeMedicineData.medicineQuickSelectionDetailData;

      // 新增藥品 - 下拉 (適用症狀)
      medicineGroupClassificationData.value = response.prescribeMedicineData.medicineGroupClassificationData;

      // 新增藥品 - 下拉 (藥品名稱)
      medicineOptionData.value = response.prescribeMedicineData.medicineOptionData;

      medicalRecordData.value = response;

      console.log(medicalRecordData.value)

      if (response.repairCreamPrescribeMedicineRecords.length !== 0) {
        response.repairCreamPrescribeMedicineRecords.forEach(item => {
          if (item.medicineId === 'SmallRepairCream') {
            smallRepairCream.value = item.amount;
          }

          if (item.medicineId === 'LargeRepairCream') {
            largeRepairCream.value = item.amount;
          }
        })
      }

      // 初始資料
      currentData.value = {
        departmentId: response.medicalRecord.departmentId,
        courseType: response.medicalRecord.courseType,
        medicineQuickSelection: null,                                                         // 藥品選擇
        subjectiveDescription: response.medicalRecord.subjectiveDescription ? response.medicalRecord.subjectiveDescription : null, // 主訴
        beautyConsumableName: response.beautyConsumableName,
        diagnosis: response.medicalRecord.diagnosis ? response.medicalRecord.diagnosis : null,                                                                      // 診斷
        estimate: response.medicalRecord.estimate ? response.medicalRecord.estimate : null,   // 諮詢估量 
        advisory: response.medicalRecord.advisory ? response.medicalRecord.advisory : null,   // 醫師醫囑
        getR: response.repairCreamPrescribeMedicineRecords.length !== 0 ? true : false,
        prescribedMedicine: prescribeMedicineRecordsToDeleteOredit.value.length !== 0 ? true : false,
        medicineQuickCheckboxData: [],
        doctorSigned: response.medicalRecord.doctorSigned,
        notTreat: response.medicalRecord.notTreat,
        customerSignatureId: response.doctorFreeRecord ? response.doctorFreeRecord.customerSignatureId : null,
        doctorSignatureId: response.doctorFreeRecord ? response.doctorFreeRecord.doctorSignatureId : null,
        doctorFreeRecordId: response.doctorFreeRecord ? response.doctorFreeRecord.id : null,
        treatmentConsume: response.medicalRecord.treatmentConsume ? response.medicalRecord.treatmentConsume : null,
        ivConsumableId: response.medicalRecord.ivConsumableId ? response.medicalRecord.ivConsumableId : null,
        suggestion: response.medicalRecord.suggestion ? response.medicalRecord.suggestion : null,
      }

      if (response.medicalRecord.courseType === 'IV') {
        changeIvConsumable();
      }
    }

    /** 新增 "新增藥品" 欄位 */
    function addAddMedicineInput() {
      addMedicineData.value.push({
        medicineTypeData: null,
        medicineGroupId: null,
        medicineId: null,
        amount: 0,
        takeMedicineFrequencyId: null,
      });
    }

    /** 刪除 "新增藥品" 欄位 */
    function deleteAddMedicineInput(index) {
      addMedicineData.value.splice(index, 1);
    }

    /** 修改 "新增藥品" 的 藥品類型 欄位 */
    function changeMedicineTypeData(index) {
      addMedicineData.value[index].medicineGroupId = null;
      addMedicineData.value[index].medicineId = null;
      addMedicineData.value[index].amount = 0;
      addMedicineData.value[index].takeMedicineFrequencyId = addMedicineData.value[index].medicineTypeData === 'Oral' ? takeMedicineFrequencyData.value[0].id : null;
    }

    /** 修改 "新增藥品" 的 適用症狀 欄位 */
    function changeMedicineGroupId(index) {
      addMedicineData.value[index].medicineId = null;
      addMedicineData.value[index].amount = 0;
      addMedicineData.value[index].takeMedicineFrequencyId = addMedicineData.value[index].medicineTypeData === 'Oral' ? takeMedicineFrequencyData.value[0].id : null;
    }

    /** 刪除曾選過的 藥品選擇 */
    function deletePrescribeMedicineRecord(index) {
      prescribeMedicineRecordsToDeleteOredit.value.splice(index, 1);
    }

    /** 更新病歷主述 */
    async function updateSubjectiveDescription() {
      const url = `/api/MedicalRecord/UpdateSubjectiveDescription`;

      const PATCHData = {
        medicalRecordId: medicalRecordId.value,
        subjectiveDescription: currentData.value.subjectiveDescription,
      }

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'PATCH',
        body: JSON.stringify(PATCHData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);
    }

    /** 確認 - 更新病歷(不含收費) */
    async function updateMedicalRecord() {

      // 醫師詳答
      let doctorDetailAnswerData = [];

      detailedAnswer.value.forEach(itemA => {
        if (Array.isArray(itemA)) {
          itemA.forEach(itemB => {
            doctorDetailAnswerData.push(
              {
                doctorDetailGroupId: itemB.groupId,
                doctorDetailGroupItemId: itemB.id
              }
            );
          })

          return null;
        }
        
        doctorDetailAnswerData.push(
          {
            doctorDetailGroupId: itemA.groupId,
            doctorDetailGroupItemId: itemA.id
          }
        );
      })

      // 醫師確認
      let prescribeMedicine = [];

      if (currentData.value.getR) {
        if (smallRepairCream.value) {
          prescribeMedicine.push({
            medicineGroupId: "AfterLaser",
            medicineId: "SmallRepairCream",
            amount: smallRepairCream.value,
            takeMedicineFrequencyId: null,
          });
        }

        if (largeRepairCream.value) {
          prescribeMedicine.push({
            medicineGroupId: "AfterLaser",
            medicineId: "LargeRepairCream",
            amount: largeRepairCream.value,
            takeMedicineFrequencyId: null,
          });
        }
      }

      if (currentData.value.prescribedMedicine) {

        const filterPrescribeMedicine = prescribeMedicineData.value.filter(item => {
          return currentData.value.medicineQuickCheckboxData.includes(item.medicineId);
        })

        filterPrescribeMedicine.forEach(item => {
          prescribeMedicine.push({
            medicineGroupId: item.medicineGroupId,
            medicineId: item.medicineId,
            amount: item.amount,
            takeMedicineFrequencyId: item.takeMedicineFrequencyId,
          });
        })
      }

      // 新增藥品
      addMedicineData.value.forEach(item => {
        if (item.medicineTypeData !== null) {
          prescribeMedicine.push({
            medicineGroupId: item.medicineGroupId,
            medicineId: item.medicineId,
            amount: item.amount,
            takeMedicineFrequencyId: item.takeMedicineFrequencyId,
          })
        }
      })

      // 曾選過的藥品
      prescribeMedicineRecordsToDeleteOredit.value.forEach(item => {
        if (item.medicineTypeData !== null) {
          prescribeMedicine.push({
            medicineGroupId: item.medicineGroupId,
            medicineId: item.medicineId,
            amount: item.amount,
            takeMedicineFrequencyId: item.takeMedicineFrequencyId,
          })
        }
      })

      const medicalRecord = medicalRecordData.value.medicalRecord;

      const PATCHData = {
        medicalRecord: {
          ...medicalRecord,
          subjectiveDescription: currentData.value.subjectiveDescription,
          estimate: currentData.value.estimate,
          advisory: currentData.value.advisory,
          doctorSigned: currentData.value.doctorSigned,
          notTreat: currentData.value.notTreat,
          prescribedMedicine: currentData.value.prescribedMedicine,
