    /** 取得初始化編輯雷射紀錄的資料 */
    async function getInitUpdateLaserRecordData(id, customerId) {
      laserRecordId = id;
      const url = `/api/LaserRecord/GetInitUpdateLaserRecordData?laserRecordId=${id}&customerId=${customerId}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      const {
        bodyParts,
        diagnosticSymptoms,
        laserEquipmentCustomizedParts,
        laserRecord,

        // 先前資料
        laserRecordDetails,
        bodyPartIdList,
        diagnosticSymptomIdList,
        customizedPartIdList,
      } = response.data;

      // 治療儀器預設
      laserEquipmentModalData.value = laserRecord.laserEquipmentId;

      bodyPartsData.value = bodyParts;
      diagnosticSymptomsData.value = diagnosticSymptoms;
      laserEquipmentCustomizedPartsData.value = laserEquipmentCustomizedParts;

      // 能量
      featureParameterData.value = [];

      const key = Object.keys(response.data.featureParameterData);

      key.forEach(itemA => {
        let laserRecord = laserRecordDetails.find(item => item.laserEquipmentFeatureId === response.data.featureParameterData[itemA][0].laserEquipmentFeatureId) || null;

        featureParameterData.value.push({
          laserEquipmentFeatureName: response.data.featureParameterData[itemA][0].laserEquipmentFeatureName,
          value: laserRecord ? laserRecord.value : null,
          list: response.data.featureParameterData[itemA],
          laserEquipmentFeatureId: response.data.featureParameterData[itemA][0].laserEquipmentFeatureId
        });
      });

      // 雷射紀錄 checkbox
      laserCheckboxData.value = {
        customizedPartIdList: laserEquipmentCustomizedParts.filter(item => customizedPartIdList.includes(item.id)),
        bodyPartIdList: bodyParts.filter(item => bodyPartIdList.includes(item.id)),
        diagnosticSymptomIdList: diagnosticSymptoms.filter(item => diagnosticSymptomIdList.includes(item.id))
      }
    }

