    /** 儲存 - 新增醫師免單紀錄 */
    async function postDoctorFreeRecord() {
      let data = Object.assign({}, freeBill.value);

      const doctorSignature = data.doctorSignature ? data.doctorSignature.split(',')[1] : null;
      const customerSignature = data.customerSignature ? data.customerSignature.split(',')[1] : null;

      data.doctorSignature = doctorSignature;
      data.customerSignature = customerSignature;

      let method = 'post';

      if (!isAddjudge) {
        method = 'PATCH';
        
        const {
          id,
          medicalRecordId,
          doctorId,
          reason,
          doctorSignatureId,
          customerSignatureId,
        } = freeBill.value;

        data = {
          doctorFreeRecord: {
            id,
            medicalRecordId,
            doctorId,
            reason,
            doctorSignatureId,
            customerSignatureId,
          },
          customerId: params.customerId,
          doctorSignature,
          customerSignature
        }
      }

      const url = `/api/DoctorFreeRecord`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method,
