    /** 歷史病歷資料整理 */
    function historicalMedicalRecordDataCollation(response) {
      getEmployeeStampData(response.medicalRecord.operator);

      const {
        voucherOrderDetails,
        subcourseData,
        medicalRecord,
        anestheticOrderDetail,
        coursePartOrderDetails,
        bodyPartsOptions,
        careRecord,
        repairCreamPrescribeMedicineRecords,
        prescribeMedicineRecords,
        doctorFreeRecord,
        customerDemandNames,
        laserRecordRelativeData,
        anestheticUsageRecordData,
        doctorDetailAnswerData,
      } = response;

      // 療程專用卷 - 整理
      let voucherOrder = voucherOrderDetails.map(itemA => {
        const bodyPartName = response.bodyPartsOptions.find(itemB => itemA.bodyPartId === itemB.id);

        return {
          bodyPartId: itemA.bodyPartId,
          bodyPartName: bodyPartName ? bodyPartName.name : null,
          voucherId: [],
        }
      })

      voucherOrderDetails.forEach(itemA => {
        voucherOrder.forEach(itemB => {
          if (itemA.bodyPartId === itemB.bodyPartId) {
            itemB.voucherId.push(itemA.voucherId);
          }
        })
      })

      // 收費明細

      let feeDetails = [];

      // 收費明細 - 麻藥費用
      if (medicalRecord.anesthesiaSituation === "Anesthesia") {
        feeDetails.push({
          chargeType: 'anesthesia',
          chargeName: '麻藥費用',
          anesthesiaSituation: "Anesthesia",
          price: anestheticOrderDetail.price === 0 ? '免費' : anestheticOrderDetail.price,
        });
      }
      // 收費明細 - 現金收費

      // 單次 - 一般部位

      const onceData = {
        chargeType: 'cash',
        chargeName: '單次費用',
        data: [],
      }

      coursePartOrderDetails.forEach(item => {
        if (item.priceType === 'Once' && item.bodyPartId !== "Other") {
          const bodyPartName = bodyPartsOptions.find(itemA => itemA.id === item.bodyPartId);
          onceData.data.push({
            bodyPartName: bodyPartName ? bodyPartName.name : null,
            price: item.price,
          });
        }
      })

      // 單次 - 其他部位
      coursePartOrderDetails.forEach(item => {
        if (item.priceType === 'Once' && item.bodyPartId == "Other") {
          onceData.data.push({
            bodyPartName: `其他/ ${item.otherPartName}`,
            price: item.price,
          });
        }
      })

      if (onceData.data.length !== 0) {
        feeDetails.push(onceData);
      }

      // 體驗

      const experienceData = {
        chargeType: 'cash',
        chargeName: '體驗費用',
        data: [],
      }

      coursePartOrderDetails.forEach(item => {
        if (item.priceType === 'Experience') {
          experienceData.data.push({
            bodyPartName: bodyPartsOptions.find(itemA => itemA.id === item.bodyPartId).name,
            price: item.price,
          });
        }
      })

      if (experienceData.data.length !== 0) {
        feeDetails.push(experienceData);
      }

      // 押金

      const depositData = {
        chargeType: 'cash',
        chargeName: '押金費用',
        data: [],
      }

      coursePartOrderDetails.forEach(item => {
        if (item.priceType === 'Deposit') {
          depositData.data.push({
            bodyPartName: bodyPartsOptions.find(itemA => itemA.id === item.bodyPartId).name,
            price: item.price,
          });
        }
      })

      if (depositData.data.length !== 0) {
        feeDetails.push(depositData);
      }

      // 雷射能量
      laserRecord.value = [];

      laserRecordRelativeData.forEach(item => {
        let Refrigerant1;
        let Refrigerant2;

        item.laserRecordDetailData.forEach(item => {
          if (item.laserEquipmentFeatureId === 'Refrigerant1') {
            Refrigerant1 = item.value || null;
          }

          if (item.laserEquipmentFeatureId === 'Refrigerant2') {
            Refrigerant2 = item.value || null;
          }
        })

        laserRecord.value.push({
          ...item,
          showLaserDiagnosis: [...item.diagnosticSymptoms, ...item.parts, ...item.laserEquipmentCustomizedPart],
          Refrigerant1,
          Refrigerant2
        })
      })
      
      historicalMedicalRecordData.value = {
        voucherOrder,
        subcourseData,
        feeDetails,
        careRecord, // 關心紀錄
        ...medicalRecord,
        notTreat: medicalRecord.notTreat,
        repairCreamPrescribeMedicineRecords,
        prescribeMedicineRecords,
        customerDemandNames,
        cancelled: doctorFreeRecord ? !doctorFreeRecord.cancelled : null,
        reason: doctorFreeRecord ? doctorFreeRecord.reason : null,
        anestheticUsageRecordData,
        doctorDetailAnswerData,
      }

      console.log(historicalMedicalRecordData.value);
    }

