      if (maximumPages.value <= 1) return null;

      maximumPages.value = Math.ceil(maximumPages.value);

      for(let i = 1; i <= maximumPages.value; i++) {
        pagination.value.push(i);
      }
    }
    
    /** 前一頁 */
    function previousPage() {
      if (minimumRange.value === 1) return null;

      minimumRange.value -= 3;
      maximumRange.value -= 3;
    } 

    /** 下一頁 */
    function nextPage() {
      let paginationNum = pagination.value.length / 3;
      let maximumRangeNum = maximumRange.value / 3;

      if (paginationNum <= maximumRangeNum) return null;
      minimumRange.value += 3;
      maximumRange.value += 3;
    }

    /** 前往最後一頁 */
    function goLastPage() {
      let paginationNum = pagination.value.length / 3;
      minimumRange.value = Math.ceil(paginationNum) * 3 - 2;
      maximumRange.value = Math.ceil(paginationNum) * 3;

      paginationFocusNum.value = pagination.value.length;
