    /** 確認客戶需求 */
    async function confirmCustomDemand() {

      const date = params.date;

      const customerDemandData = customerNeedsCheckboxData.value.map(item => item.id);


      // 篩出新建立的客戶需求
      const newCustomDemands = customDemands.value.filter(item => !item.id);

