          if (!paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.some(item => item.id === 'Voucher')) {
            paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
          }

          paymentMethodData.value[paymentMethodData.value.length - 1].created.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
        }

        const oweVoucher = initUpdateBasicDataAndFeeData.value.oweVoucherSituationData[0];

        if (oweVoucher) {
          oweVoucherInput.value[oweVoucherInput.value.length - 1].oweAmount = oweVoucher.oweAmount;

          // 如果支付方式內沒有 'Voucher'，就加入 'Voucher'
          if (!paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.some(item => item.id === 'Voucher')) {
            paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
          }

          if (!paymentMethodData.value[paymentMethodData.value.length - 1].created.some(item => item.id === 'Voucher')) {
            paymentMethodData.value[paymentMethodData.value.length - 1].created.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
          }
        }
