      bodyPartsOptions.forEach(item => {
        
        if (initUpdateBasicDataAndFeeData.value.bodyParts.some(t => t.bodyPartId === item.id)) {
          const bodyPart = initUpdateBasicDataAndFeeData.value.bodyParts.find(t => t.bodyPartId === item.id);

          bodyPartsArr.push({
