      })

      bodyParts.value = bodyPartsArr;

      // 整理治療下拉資料
      initUpdateBasicDataAndFeeData.value.bodyPartsOptions = initUpdateBasicDataAndFeeData.value.bodyPartsOptions.map(itme => {
        return {
          bodyPartId: itme.id,
          name: itme.name,
          extra: bodyParts.value.some(t => t.bodyPartId === itme.id && !t.extra) ? false : true,
        }
      })

      console.log(initUpdateBasicDataAndFeeData.value.bodyPartsOptions);
      
      // 一般部位初始
      bodyParts.value.forEach(item => {
        if (item.bodyPartId !== 'Other') {

          allBodyParts.value.push(item);

          paymentMethodData.value.push({
            bodyPartId: item.bodyPartId,
