              cashData.value[cashData.value.length - 1].push({
                ...itemA,
                disabled: itemA.received,
              });

              paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.push({ id:'Cash', name: '現金支付', sequence: 3, active: true });

              // 如果已支付，就不可刪除
              if (itemA.received) {
                paymentMethodData.value[paymentMethodData.value.length - 1].created.push({ id:'Cash', name: '現金支付', sequence: 3, active: true });
              }
            }
          })

          // 療程專用券 初始
          voucherInput.value.push('');
          oweVoucherInput.value.push({
            bodyPartId: item.bodyPartId,
