          });

          voucherOrderDetailData.value.push([]);
          // createdVoucherOrderDetailData.value.push([]);

          // 療程專用券 - 初始
          initUpdateBasicDataAndFeeData.value.voucherOrderDetails.forEach(itemA => {

            if(itemA.bodyPartId === item.bodyPartId) {

              voucherOrderDetailData.value[voucherOrderDetailData.value.length - 1].push(itemA);

              // 如果支付方式內沒有 'Voucher'，就加入 'Voucher'
              if (!paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.some(item => item.id === 'Voucher')) {
                paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
              }

              // 不是加診，不可刪除
              if (!item.extra) {
                paymentMethodData.value[paymentMethodData.value.length - 1].created.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
              }
            }
          })
          
          // 療程專用券 - 欠卷 初始
          initUpdateBasicDataAndFeeData.value.oweVoucherSituationData.forEach(itemA => {
            if(itemA.bodyPartId === item.bodyPartId) {
              oweVoucherInput.value[oweVoucherInput.value.length - 1].oweAmount = itemA.oweAmount;

              // 如果支付方式內沒有 'Voucher'，就加入 'Voucher'
              if (!paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.some(item => item.id === 'Voucher')) {
                paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
              }

              // 不是加診，不可刪除
              if (!item.extra) {
                // 如果支付方式(非加診 created)內沒有 'Voucher'，就加入 'Voucher'
                if (!paymentMethodData.value[paymentMethodData.value.length - 1].created.some(item => item.id === 'Voucher')) {
                  paymentMethodData.value[paymentMethodData.value.length - 1].created.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
                }
              }
            }
          })

          // 活動優惠卷 - 預設
          activityData.value.push([]);

          initUpdateBasicDataAndFeeData.value.couponOrderDetails.forEach(itemA => {
            if(itemA.bodyPartId === item.bodyPartId) {
              const len = activityData.value.length - 1;

              activityData.value[len].push(
                {
                  prizeId: itemA.prizeId,
                  bodyPartId: itemA.bodyPartId,
                  couponNumber: itemA.couponNumber
                }
              );

              // 如果支付方式內沒有 'Activity'，就加入 'Activity'
              if (!paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.some(item => item.id === 'Activity')) {
                paymentMethodData.value[paymentMethodData.value.length - 1].paymentMethod.push({ id: 'Activity', name: '活動優惠券', sequence: 2, active: true });
              }

              // 不是加診，不可刪除
              if (!item.extra) {
                paymentMethodData.value[paymentMethodData.value.length - 1].created.push({ id: 'Activity', name: '活動優惠券', sequence: 2, active: true });
              }
            }
          })
        }
      });

      // 其他部位 - 現金支付、治療部位初始
      initUpdateBasicDataAndFeeData.value.otherParts.forEach(item => {
        otherBodyParts.value.push({
          part: item.part,
          scope: item.scope,
          price: item.price,
          extra: item.extra,
        })

        allBodyParts.value.push({
