          name: "其他",
          part: item.part,
          scope: item.scope,
          price: item.price
        });

        paymentMethodData.value.push({
          bodyPartId: "Other",
          paymentMethod: [{id: 'Cash', name: '現金支付', sequence: 3, active: true}],
          created: [],
        })

        paymentMethodSwitch.value.push({
          bodyPartId: "Other",
          switch: false,
        })

      })
      // console.log(initUpdateBasicDataAndFeeData.value.coursePartOrderDetails);
      
      // initUpdateBasicDataAndFeeData.value.coursePartOrderDetails.forEach(item => {
      //   if (item.bodyPartId === "Other") {
          
      //     cashData.value.push([]);

      //     cashData.value[cashData.value.length - 1].push(item);
      //   }
      // })

      otherBodyParts.value.forEach(item => {
        const coursePartOrderItem = initUpdateBasicDataAndFeeData.value.coursePartOrderDetails.find(t => item.part === t.otherPartName && t.bodyPartId === "Other");
        
        if (coursePartOrderItem) {
          cashData.value.push([]);

          cashData.value[cashData.value.length - 1].push(coursePartOrderItem);
        }
      })

      otherBodyPartLength.value = otherBodyParts.value.length;
      if (otherBodyPartLength.value !== 0) {
        otherBodyPartJudge.value = true;
      }
      cashInputBlur();

      editMedicalRecordRefModal.show();
    }

    const initUpdateBasicDataAndFeeData = ref({});

    /** 取得初始化編輯病歷基本資訊和收費用的資料 */
    async function getInitUpdateBasicDataAndFeeData() {
      console.log(medicalRecordId);
      const url = `/api/MedicalRecord/GetInitUpdateBasicDataAndFeeData?medicalRecordId=${medicalRecordId.value}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      return response.data;
    }

    /** 取得病歷對應的客戶需求名稱清單 */
    async function getCustomerDemandNamesById() {
      const url = `/api/MedicalRecord/GetCustomerDemandNamesById?medicalRecordId=${medicalRecordId.value}`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      showCustomerNeeds.value = response.data;
    }

    /** 開啟治療部位下拉選單 */
    const openAppointmentBodyPartsWindow = () => {
      if (bodyPartsWindow.value) return null;
      bodyPartsWindow.value = true;

      function close() {
        bodyPartsWindow.value = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 刪除治療部位選項 */
    const deleteAppointmentBodyPartsItem = (item) => {

      if (!item.extra) return null;

      let index = bodyParts.value.indexOf(item);
      // const newNoDataForOtherParts = bodyParts.value.filter(itemA => itemA.id !== 'Other');

      bodyParts.value.splice(index, 1);

      if (item.bodyPartId !== 'Other') {
        // const deleteItem = allBodyParts.value.find(item => {
        //   return !newNoDataForOtherParts.includes(item);
        // });

        const i = allBodyParts.value.indexOf(item);

        allBodyParts.value.splice(i, 1);
        noDataForOtherParts.splice(i, 1);

        paymentMethodData.value.splice(i, 1);
        paymentMethodSwitch.value.splice(i, 1);

        let deleteAmount = 0;

        if (cashData.value[i].length !== 0) {
          if (cashData.value[i][0].priceType === 'Deposit') {
          cashData.value[i][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

            total.value -= deleteAmount;
          } else {
            total.value -= cashData.value[i][0].price;
          }
        }

        cashData.value.splice(i, 1);

        voucherInput.value.splice(i, 1);
        oweVoucherInput.value.splice(i, 1);
        voucherOrderDetailData.value.splice(i, 1);
      } else {
        otherBodyPartJudge.value = false;
        otherBodyPartLength.value = 0;
        otherBodyParts.value = [];
        for(let i = 0; i < allBodyParts.value.length; i++) {
          if (allBodyParts.value[i].bodyPartId === "Other") {
            allBodyParts.value.splice(i, 1);

            paymentMethodData.value.splice(i, 1);
            paymentMethodSwitch.value.splice(i, 1);

            if (cashData.value[i].length !== 0) {
              total.value -= cashData.value[i][0].price;
            }
            cashData.value.splice(i, 1);

            // voucherInput.value.splice(i, 1);
            // voucherOrderDetailData.value.splice(i, 1);

            i--;
          }
        }
      }
    }

    /** 選擇部位 */
    function selectedBodyParts(item, event) {

      const checked = event.target.checked;
      const newNoDataForOtherParts = bodyParts.value.filter(itemA => itemA.bodyPartId !== 'Other');
      const newNoDataForOtherPartsLen = newNoDataForOtherParts.length - 1;

      if (checked && item.bodyPartId !== 'Other') {

        const addItem = newNoDataForOtherParts[newNoDataForOtherPartsLen];

        allBodyParts.value.splice(newNoDataForOtherPartsLen, 0, addItem);

        paymentMethodData.value.splice(newNoDataForOtherPartsLen, 0, {
          bodyPartId: addItem.bodyPartId,
          paymentMethod: [{id: 'Voucher', name: '療程專用券', sequence: 1, active: true}],
          created: [],
        });
        paymentMethodSwitch.value.splice(newNoDataForOtherPartsLen, 0, []);

        cashData.value.splice(newNoDataForOtherPartsLen, 0, []);

        voucherInput.value.splice(newNoDataForOtherPartsLen, 0, '');
        oweVoucherInput.value.splice(newNoDataForOtherPartsLen, 0, {
          bodyPartId: addItem.bodyPartId,
          oweAmount: 0
        });

        voucherOrderDetailData.value.splice(newNoDataForOtherPartsLen, 0, []);
      }

      if (!checked && item.bodyPartId !== 'Other') {
        // 治療部位刪除時
        
        const deleteItem = allBodyParts.value.find(item => {
          return !newNoDataForOtherParts.includes(item);
        });

        const i = allBodyParts.value.indexOf(deleteItem);

        allBodyParts.value.splice(i, 1);
        noDataForOtherParts.splice(i, 1);

        paymentMethodData.value.splice(i, 1);
        paymentMethodSwitch.value.splice(i, 1);

        let deleteAmount = 0;

        if (cashData.value[i].length !== 0) {
          if (cashData.value[i][0].priceType === 'Deposit') {
          cashData.value[i][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

            total.value -= deleteAmount;
          } else {
            total.value -= cashData.value[i][0].price;
          }
        }

        cashData.value.splice(i, 1);

        voucherInput.value.splice(i, 1);
        oweVoucherInput.value.splice(i, 1);
        voucherOrderDetailData.value.splice(i, 1);
      }

      if (checked && item.bodyPartId === 'Other') {
        otherBodyPartLength.value = 1;
        otherBodyParts.value.push({
          part: "",
          scope: "",
          price: "",
          extra: true,
        })

      otherBodyPartJudge.value = true;

        allBodyParts.value.push({
          bodyPartId: "Other",
          name: "其他",
          part: "",
          price: '',
          scope: ""
        });

        paymentMethodData.value.push({
          bodyPartId: "Other",
          paymentMethod: [{id: 'Cash', name: '現金支付', sequence: 3, active: true}],
          created: [],
        })

        paymentMethodSwitch.value.push({
          bodyPartId: "Other",
          switch: false,
        })

        cashData.value.push([]);

        // voucherInput.value.push('');
        // voucherOrderDetailData.value.push([]);

        // oweVoucherInput.value.push({
        //   bodyPartId: item.id,
        //   oweAmount: 0
        // });

        cashData.value[cashData.value.length - 1].push(
          {
            bodyPartId: "Other",
            paymentMethodId: "Cash",
            priceType: "Once",
            price: null,
            received: false,
            otherPartName: '',
            otherPartScope: '',
            depositData: [],
          }
        );
      }

      if (!checked && item.bodyPartId === 'Other') {
        otherBodyPartJudge.value = false;
        otherBodyPartLength.value = 0;
        otherBodyParts.value = [];
        for(let i = 0; i < allBodyParts.value.length; i++) {
          if (allBodyParts.value[i].id === "Other") {
            allBodyParts.value.splice(i, 1);

            paymentMethodData.value.splice(i, 1);
            paymentMethodSwitch.value.splice(i, 1);

            total.value -= cashData.value[i][0].price;
            cashData.value.splice(i, 1);
            i--;
          }
        }
      }
    }

    /** 其他部位 - 部位 Blur */
    function otherBodyPartInputBlur(index) {
      if (otherBodyParts.value[index].part === '') return null;

      const newNoDataForOtherParts = bodyParts.value.filter(item => item.bodyPartId !== 'Other');
      const len = newNoDataForOtherParts.length;

      allBodyParts.value[len + index].part = otherBodyParts.value[index].part;

      if (!cashData.value[len + index][0]) return null;

      cashData.value[len + index][0].otherPartName = otherBodyParts.value[index].part;
    }

    function cashInputBlur(cashDataItem, index) {

      let added = 0;
      cashData.value.forEach(item => {

        if (item.length !== 0) {
          if (!isNaN(parseInt(item[0].price))) {
            added += parseInt(item[0].price);
            return null;
          }

          if (item[0].depositData.length !== 0) {
            item[0].depositData.forEach(itemA => {
              if (!isNaN(parseInt(itemA.amountOfMoney))) {
                added += parseInt(itemA.amountOfMoney);
              }
            })
          }
        }
      })

      total.value = added;

      if (!cashDataItem) return null;
      if (cashDataItem.bodyPartId === 'Other') {
        const len = bodyParts.value.filter(item => item.bodyPartId !== 'Other').length;
        const otherBodyIndex = index - len;

        otherBodyParts.value[otherBodyIndex].price = cashData.value[index][0].price;
      }
    }

    /** 輸入的金額，計算總金額 和 將輸入的金額補入 otherBodyParts 的 price */
    function voucherTypesJudge(depositData, id, voucherType) {
      if (voucherType === id) return true;

      return !depositData.some(item => item.voucherType === id);
    }

    /** 開啟支付方式下拉選項 */
    function openPaymentMethodOption(index) {
      if (paymentMethodSwitch.value[index].switch) return null;
      
      let indexClicked;

      // 取得被展開過的優惠卷下拉視窗
      const judge = paymentMethodSwitch.value.find((item, i) => {
        if (item.switch) {
          indexClicked = i;
          return item;
        }
      })

      // 有被展開過，就關閉該視窗
      if (judge) {
        paymentMethodSwitch.value[indexClicked].switch = false;
        document.removeEventListener('click', close);
      }

      paymentMethodSwitch.value[index].switch = true;

      function close() {
        paymentMethodSwitch.value[index].switch = false;
        document.removeEventListener('click', close);
      }

      document.addEventListener('click', close);
    }

    /** 新增其他部位的欄位 */
    const addOtherBodyPartLength = () => {
      otherBodyPartLength.value++;
      otherBodyParts.value.push({
        part: "",
        scope: "",
        price: "",
        extra: true,
      })

      paymentMethodData.value.push({
        bodyPartId: "Other",
        paymentMethod: [{ id: 'Cash', name: '現金支付', sequence: 3, active: true }],
        created: [],
      })

      paymentMethodSwitch.value.push({
        bodyPartId: "Other",
        switch: false,
      })

      allBodyParts.value.push({
        bodyPartId: "Other",
        name: "其他",
        part: "",
        price: "",
        scope: ""
      });

      cashData.value.push([]);

      cashData.value[cashData.value.length - 1].push(
        {
          bodyPartId: "Other",
          paymentMethodId: "Cash",
          priceType: "Once",
          price: null,
          received: false,
          otherPartName: '',
          otherPartScope: '',
          depositData: [],
        }
      );
    }

    /** 刪除其他欄位 */
    const deleteOtherBodyPartLength = (index) => {
      otherBodyPartLength.value--;
      otherBodyParts.value.splice(index, 1);
      const newNoDataForOtherParts = bodyParts.value.filter(item => item.bodyPartId !== 'Other');

      const len = newNoDataForOtherParts.length;
      const i = len + index;
      
      allBodyParts.value.splice(i, 1);

      paymentMethodData.value.splice(i, 1);
      paymentMethodSwitch.value.splice(i, 1);

      if (cashData.value[i].length !== 0) {
        total.value -= cashData.value[i][0].price;
      }

      cashData.value.splice(i, 1);
    }

    /** 其他部位 - 範圍 Blur */
    function otherBodyScopeInputBlur(index) {
      if (otherBodyParts.value[index].scope === '') return null;

      const newNoDataForOtherParts = bodyParts.value.filter(item => item.bodyPartId !== 'Other');
      const len = newNoDataForOtherParts.length;

      allBodyParts.value[len + index].scope = otherBodyParts.value[index].scope;

      if (!cashData.value[len + index][0]) return null;

      cashData.value[len + index][0].otherPartScope = otherBodyParts.value[index].scope;
    }

    /** 刪除已選的支付方式 */
    function deleteSelectedPaymentMethod(item, index) {

      const i = paymentMethodData.value[index].paymentMethod.indexOf(item);
      paymentMethodData.value[index].paymentMethod.splice(i, 1);

      if (item.id === 'Cash') {
        let deleteAmount = 0;

        if (cashData.value[index][0].priceType === 'Deposit') {
          cashData.value[index][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

          total.value -= deleteAmount;
          cashData.value[index] = [];
          return null;
        }

        total.value -= cashData.value[index][0].price;
        cashData.value[index] = [];
      }

      if (item.id === 'Voucher') {
        voucherOrderDetailData.value[index] = [];

        oweVoucherInput.value[index].oweAmount = null;
      }
    }

    /** 支付方式選擇，新增或刪除預設空值給該部位 */
    function selectedPaymentMethod(item, index, event) {

      const checked = event.target.checked;

      if (item.id === 'Activity' && !checked) {
        if (paymentMethodData.value[index].created.some(itemA => itemA.id === item.id)) {
          paymentMethodData.value[index].paymentMethod.push({ id: 'Activity', name: '活動優惠券', sequence: 2, active: true });
          return null;
        }
      }

      if (item.id === 'Voucher' && !checked) {
        if (paymentMethodData.value[index].created.some(itemA => itemA.id === item.id)) {
          paymentMethodData.value[index].paymentMethod.push({id: 'Voucher', name: '療程專用券', sequence: 1, active: true });
          return null;
        }
        voucherOrderDetailData.value[index] = [];

        oweVoucherInput.value[index].oweAmount = null;
      }

      if (item.id === 'Cash' && !checked) {
        let deleteAmount = 0;

        if (cashData.value[index][0].priceType === 'Deposit') {
          cashData.value[index][0].depositData.forEach(item => {
            deleteAmount += item.amountOfMoney;
          });

          total.value -= deleteAmount;
          cashData.value[index] = [];
          return null;
        }
        
        total.value -= cashData.value[index][0].price;
        cashData.value[index] = [];
      }

      if (item.id === 'Cash' && checked) {
        cashData.value[index].push(
          {
            bodyPartId: allBodyParts.value[index].bodyPartId,
            paymentMethodId: "Cash",
            priceType: "Once",
            price: null,
            received: false,
            otherPartName: allBodyParts.value[index].part ? allBodyParts.value[index].part : '',
            otherPartScope: allBodyParts.value[index].scope ? allBodyParts.value[index].scope : '',
            depositData: [],
          }
        );
      }
    }

    // --- 療程專用券 ---

    /** 療程專用券 - 輸入 */
    function voucherDataEnter(index) {
      voucherOrderDetailData.value[index].push({
        bodyPartId: allBodyParts.value[index].bodyPartId,
        voucherId: voucherInput.value[index],
      });

      voucherInput.value[index] = '';
    }

    /** 療程專用券 - 刪除 */
    function deleteVoucherData(item, index) {
      const i = voucherOrderDetailData.value[index].indexOf(item);

      voucherOrderDetailData.value[index].splice(i, 1);
    }

    /** 改變現金支付種類 */
    function changePriceType(item, index) {

      total.value -= item.price;
      cashData.value[index][0].price = null;

      if (item.priceType === "Deposit") {
        cashData.value[index][0].price = null;
        cashData.value[index][0].depositData.push({
          voucherType: initUpdateBasicDataAndFeeData.value.voucherTypes[0].id,
          amountOfMoney: null
        });

        return null;
      }

      let deleteAmount = 0;

      if (item.priceType !== "Deposit" && cashData.value[index][0].depositData.length !== 0) {
        cashData.value[index][0].depositData.forEach(item => {
          deleteAmount += item.amountOfMoney;
        });

        total.value -= deleteAmount;
        cashData.value[index][0].depositData = [];

        return null;
      }
    }

    function addDepositInput(index, depositData) {
      const arr = initUpdateBasicDataAndFeeData.value.voucherTypes.filter(item => !depositData.some(x => x.voucherType === item.id));
      
      cashData.value[index][0].depositData.push({
        voucherType: arr[0].id,
        amountOfMoney: null
      });
    }

    function deleteDepositInput(index, i) {
      let deleteAmount = cashData.value[index][0].depositData[i].amountOfMoney;

      total.value -= deleteAmount;

      cashData.value[index][0].depositData.splice(i, 1);
    }

    /** 確定 - 到診資訊收費內容 */
    async function executeUpdateMedicalRecord() {
      const {
        medicalRecordBasicData,
        anestheticOrderDetail,
      } = initUpdateBasicDataAndFeeData.value;

      const medicalRecordId = initUpdateBasicDataAndFeeData.value.medicalRecordBasicData.id;

      const bodyPartsPostData = bodyParts.value.map(t => {
        return {
          medicalRecordId: medicalRecordId,
          bodyPartId: t.bodyPartId,
          extra: t.extra
        }
      }).filter(t => t.bodyPartId !== 'Other');

      // 是否上麻
      // const anesthetic = commonDataList.value.anestheticFees.find(item => item.id === modalData.value.anestheticFee);

      // 現金支付
      const coursePartOrderDetailData = [];

      const cashDataCopy = JSON.parse(JSON.stringify(cashData.value));

      cashDataCopy.forEach(item => {

        if (item[0]) {
          delete item[0].disabled;
          coursePartOrderDetailData.push(item[0]);
        }
      })
      // 現金支付 驗證
      let coursePartOrderDetailDataIsNull = false;

      coursePartOrderDetailData.forEach(item => {
        const depositDataJudge = item.depositData.length !== 0;

        if (depositDataJudge && item.depositData.some(t => !t.amountOfMoney)) {
          updateMessage('未填寫金額或填入多餘的符號');
          coursePartOrderDetailDataIsNull = true;
          return null;
        }

        if (!depositDataJudge && !item.price) {
          updateMessage('未填寫金額或填入多餘的符號');
          coursePartOrderDetailDataIsNull = true;
          return null;
        }
      })

      if (coursePartOrderDetailDataIsNull) return null;

      // 欠卷，驗證
      let judge = [];

      paymentMethodData.value.forEach((itemA, index) => {
        itemA.paymentMethod.forEach(itemB => {
          if (itemB.id === 'Voucher') {
            if (oweVoucherInput.value[index].oweAmount === null || oweVoucherInput.value[index].oweAmount === '') {
              judge.push(index);
            }

            if (oweVoucherInput.value[index].oweAmount === 0) {
              if (voucherOrderDetailData.value[index].length === 0) {
                judge.push(index);
              }
            }
          }
        });
      })

      if (judge.length !== 0) return null;

      // 療程專用券
      const voucherOrderDetail = [];

      voucherOrderDetailData.value.forEach(itemA => {
        if (itemA.length === 0) return null;
        itemA.forEach(itemB => {
          voucherOrderDetail.push(itemB);
        });
      })

      // 療程專用券 - 欠卷
      const oweVoucherSituationData = oweVoucherInput.value.filter(item => item.oweAmount !== 0 && item.oweAmount !== null);

      // 活動優惠券
      const couponOrderDetailData = initUpdateBasicDataAndFeeData.value.couponOrderDetails;

      // console.log(couponOrderDetailData, initUpdateBasicDataAndFeeData.value.couponOrderDetailData, 'couponOrderDetailData');

      let otherPartsTobodyParts = [];

      // 特殊狀況: otherParts 有值，bodyParts 也須新增一個 otherParts 的值
      if (otherBodyParts.value.length !== 0) {
        otherPartsTobodyParts.push({
          medicalRecordId: medicalRecordId,
          bodyPartId: "Other",
          extra: true
        });
      }

      const PATCHData = {
        medicalRecord: {...medicalRecordBasicData},
        bodyParts: [...bodyPartsPostData, ...otherPartsTobodyParts],
        otherParts: otherBodyParts.value,
        anestheticOrderDetailData: anestheticOrderDetail ? {
          anesthesiaType: anestheticOrderDetail.anesthesiaType,
          anestheticFeeId: anestheticOrderDetail.anestheticFeeId,
          price: anestheticOrderDetail.price,
          received: anestheticOrderDetail.received,
        } : null,
        coursePartOrderDetailData: coursePartOrderDetailData || null,
        voucherOrderDetailData: voucherOrderDetail,
        oweVoucherSituationData,
        couponOrderDetailData,
      }

      console.log(JSON.stringify(PATCHData));

      // 更新病歷基本資料和收費
      const url = `/api/MedicalRecord/UpdateBasicDataAndFee`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'PATCH',
        body: JSON.stringify(PATCHData),
      }

      const response = await fetchApi(url, options);

      updateMessage(response.message);

      if (response.statusCode >= 400) return null;

      editMedicalRecordRefModal.hide();
    }
    
    /** 視窗關閉 */
    function windowClose() {
      window.close();
    }

    const updateBasicDataAndFeeData = ref([]);

    /** 更新病歷基本資料和收費 */
    async function updateBasicDataAndFee(customerId, courseId) {
      const url = `/api/MedicalRecord/GetOtherPartHistory?customerId=${customerId}&courseId=${courseId}&limit=5`;

      const token = getTokenFromCookie();

      const options = {
        headers: {
          'Content-Type': 'application/json',
          "Authorization" : `Bearer ${token}`
        },
        method: 'GET'
      }

      const response = await fetchApi(url, options);

      updateBasicDataAndFeeData.value = response.data;
    }

    /** 選擇病歷基本資料和收費 */
    function selectUpdateBasicDataAndFeeData(item) {
      otherBodyPartLength.value++;
      otherBodyParts.value.push({
        part: item.part,
        scope: item.scope,
        price: item.price,
        extra: true,
      })

      paymentMethodData.value.push({
        bodyPartId: "Other",
        paymentMethod: [{ id: 'Cash', name: '現金支付', sequence: 3, active: true }],
        created: [],
      })

      paymentMethodSwitch.value.push({
        bodyPartId: "Other",
        switch: false,
      })

      allBodyParts.value.push({
        id: "Other",
        name: "其他",
        part: item.part,
        scope: item.scope,
        price: item.price
      });

      cashData.value.push([]);

      cashData.value[cashData.value.length - 1].push(
        {
          bodyPartId: "Other",
          paymentMethodId: "Cash",
          priceType: "Once",
          price: item.price,
          received: false,
          otherPartName: item.part,
          otherPartScope: item.scope,
          depositData: [],
        }
      );

      total.value += cashData.value[cashData.value.length - 1][0].price;
    }

    /** 改變耗材選擇 */
    function changeIvConsumable() {
      const ivConsumableItem = ivConsumable.value.find(t => t.id === currentData.value.ivConsumableId) || null;

      if (!ivConsumableItem) {
        currentData.value.subjectiveDescription = null;
        currentData.value.diagnosis = null;
        currentData.value.treatmentConsume = null;
        return null;
      }

      const {
        subjectiveDescription,
        diagnosis,
        treatmentConsume
      } = ivConsumableItem;

      currentData.value.subjectiveDescription = subjectiveDescription;
      currentData.value.diagnosis = diagnosis;
      currentData.value.treatmentConsume = treatmentConsume;
    }

    return {
      medicalRecordId,
      arrivedTime,
      operator,
