      changeMedicineTypeData,
      changeMedicineGroupId,

      updateSubjectiveDescription,
      updateMedicalRecord,

      clickPrescribe,
      changeGetRSelect,
      smallRepairCream,
      largeRepairCream,
      prescribeMedicineRecordsToDeleteOredit,
      deletePrescribeMedicineRecord,

      // 麻藥紀錄
      anestheticList,
      anestheticRecords,
      // beauticianList,
      addOrEditAnesthesiaRecord,
      anestheticUsageRecord,
      inputDoseJudge,

      // 雷射紀錄
      laserEquipmentModalData,
      laserEquipmentList,
      bodyPartsData,
      diagnosticSymptomsData,
      laserEquipmentCustomizedPartsData,
      changeLaserEquipment,
      featureParameterData,
      postLaserEquipmentCustomizedPart,
      laserEquipmentCustomizedPartData,
      laserCheckboxData,
      postLaserRecord,
      laserRecord,

      // 刪除紀錄(麻藥&雷射)
      confirmDelete,

      // 醫師免單
      freeBillCheckbox,
