      postDoctorFreeRecord,
      freeRecord,
      closeSignatureFunctionModal,
      clearCanvas,
      saveSignature,
      docterSignature,
      showSignatureImg,
      confirmCancelfreeBill,

      // 子療程
      subcourseData,
