      changeSubcourseOperator,

      // 客戶特質
      customerTraitList,
      customerBasicData,
      changeTrait,
      addCustomDemand,
      deleteCustomDemand,
      confirmCustomDemand,
      
      // 客戶需求
      customerNeedsData,
      customerNeedsCourseType,
      customerNeedsCheckboxData,
      customDemands,
      customDemand,
      showCustomerNeeds,

      // 病歷內的編輯病歷
      initUpdateBasicDataAndFeeData,
      editMedicalRecordData,
      bodyParts,
      bodyPartsWindow,
      openAppointmentBodyPartsWindow,
      deleteAppointmentBodyPartsItem,
      otherBodyPartInputBlur,
      selectedBodyParts,
      otherBodyPartLength,
      otherBodyParts,
